  margin: 0;
  max-height: 240px;
  width: 300px;
}

.scrollable-order-book-ul {
  padding: 0;
  margin: 0;
  max-height: 240px;
  width: 300px;
  overflow-y: scroll;
  scrollbar-width: thin;
  scrollbar-color: #d6d6d6 rgba(255, 255, 255, 0.192);
}

.buy-type {
  color: #1aafa6;
  font-weight: 600;
  margin-left: 5px;
}

.normal-type {
  color: #000;
  opacity: 0.5;
}

.sell-type {
  color: #e37a7a;
  font-weight: 600;
  margin-left: 5px;
}

.order-book-item {
  display: flex;
  flex-direction: column;
  margin-right: 100px;
  width: 1px;
  height: 20px;
}

.component-heading {
  font-family: "Rany";
  font-size: 24px;
  font-weight: 300;
  letter-spacing: 0.05em;
}

.tokens-dd {
  width: 127px;
  height: 28px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-radius: 20px;
  outline: none;
  background-color: rgba(228, 227, 227, 0.712);
  border: 1px solid #ebebeb;
  font-family: "Rany";
  font-size: 14px;
  opacity: 0.6;
  font-weight: 300;
  letter-spacing: 0.05em;
  margin-bottom: 5%;
  transition: all 0.2s ease;
}

.tokens-dd-logged-out {
  width: 127px;
  height: 28px;
  justify-content: space-around;
  align-items: center;
  border-radius: 20px;
  outline: none;
  background-color: rgba(228, 227, 227, 0.712);
  border: 1px solid #ebebeb;
  font-family: "Rany";
  font-size: 14px;
  opacity: 0.6;
  font-weight: 300;
  letter-spacing: 0.05em;
  margin-bottom: 5%;
}

.tokens-dd-logged-out:hover {
  cursor: auto;
}

.tokens-dd:hover {
  background-color: #adadad;
  color: #f2f2f2;
}

.order-book-text-columns {
